import { forwardRef } from "react"
import { watchlistCustomerIo } from "@rp/react/common/customer-io"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Checkbox, ListItemIcon, ListItemText, MenuItem, SxProps } from "@rp/react/web/mui/core"
import { DragHandle } from "@rp/react/web/mui/icons"
import { Column } from "@tanstack/table-core"
import { WatchlistDataTabsTableEntity } from "./WatchlistDataTabsTable.types"

type Props = {
  item: Column<WatchlistDataTabsTableEntity, unknown>
  sx?: SxProps
  componentProps?: { filterTerm?: string }
  dragOptions?: { disableDrag?: boolean; isDragging?: boolean }
  dataTab?: string
}

const WatchlistDataTabsColumnsMenuItem = forwardRef(function FollowedSearchItem(
  { item, dataTab }: Props,
  ref,
) {
  return (
    <MenuItem
      onClick={() => {
        const wasVisible = item.getIsVisible()
        item.toggleVisibility()
        if (dataTab) {
          const label = item.columnDef.meta?.menuLabel ?? item.id
          if (!wasVisible) {
            watchlistCustomerIo.dataView.columnsModified({
              dataViewName: dataTab,
              columnsAdded: [label],
            })
          } else {
            watchlistCustomerIo.dataView.columnsModified({
              dataViewName: dataTab,
              columnsAdded: [],
              columnsRemoved: [label],
            })
          }
        }
      }}
      sx={{
        px: 1.5,
        m: 0,
        py: 0,
        maxWidth: "300px",
        "&:hover": { backgroundColor: "transparent !important" },
      }}
    >
      <ListItemIcon
        ref={ref}
        sx={{
          "&:hover": { cursor: "grab" },
          "& svg": { color: "text.secondary" },
        }}
      >
        <DragHandle />
      </ListItemIcon>
      <ListItemIcon>
        <Checkbox edge="start" checked={item.getIsVisible()} tabIndex={-1} disableRipple />
      </ListItemIcon>
      <EllipsisTooltip
        typographyProps={{
          component: ListItemText,
          sx: { span: { display: "inline" } },
        }}
        tooltipText={item.columnDef.meta?.menuLabel ?? item.id}
      >
        {item.columnDef.meta?.menuLabel ?? item.id}
      </EllipsisTooltip>
    </MenuItem>
  )
})

export { WatchlistDataTabsColumnsMenuItem }
